.page-articles {
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;

    @media (max-width: 991px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 574px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__btn-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
